"use client";

import Link from "next/link";
import { useEffect, useState } from "react";
import { User2 } from "lucide-react";

export function Topbar() {
  // Couleurs (mêmes que StatusBar proposé)
  const deepBlue = "#0b1235";

  // Anti mismatch SSR/CSR + horloge live
  const [now, setNow] = useState<Date | null>(null);
  useEffect(() => {
    setNow(new Date());
    const id = setInterval(() => setNow(new Date()), 1000);
    return () => clearInterval(id);
  }, []);

  const timeStr =
    now?.toLocaleTimeString("en-US", { hour12: true }) ?? ""; // ex: 1:31:55 PM
  const dateStr = now?.toLocaleDateString("en-US") ?? "";      // ex: 8/7/2025

  return (
    <header
      style={{
        display: "flex",
        alignItems: "center",
        background: deepBlue,
        height: 64,
        padding: "0 16px",
        color: "#ffffff",
        position: "relative",
      }}
    >
      {/* Logo + version */}
      <div style={{ display: "flex", alignItems: "center", gap: 12 }}>
        <Link href="/" style={{ display: "flex", alignItems: "center" }}>
          {/* Mets ton vrai logo dans public/logo-stirweld.svg */}
          <img
            src="/logo-stirweld.svg"
            alt="stirweld"
            style={{ height: 28 }}
          />
        </Link>
        <span style={{ fontWeight: 800, letterSpacing: 0.5 }}>V4 v2.0</span>
      </div>

      {/* Droite : heure/date + avatar + username */}
      <div style={{ marginLeft: "auto", display: "flex", alignItems: "center", gap: 24 }}>
        {/* Heure / Date (2 lignes, heure bien grande) */}
        <div style={{ textAlign: "right", lineHeight: 1.1 }}>
          <div suppressHydrationWarning style={{ fontSize: 28, fontWeight: 900 }}>
            {timeStr}
          </div>
          <div suppressHydrationWarning style={{ fontSize: 22, fontWeight: 900 }}>
            {dateStr}
          </div>
        </div>

        {/* Avatar rond blanc + _SystemUser */}
        <div style={{ display: "flex", alignItems: "center", gap: 10 }}>
          <div
            style={{
              width: 56,
              height: 56,
              borderRadius: "50%",
              background: "#ffffff",
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              color: deepBlue,
              fontWeight: 800,
            }}
            aria-label="User"
          >
            <User2 />
          </div>
          <div style={{ fontWeight: 700, fontSize: 14, marginTop: 2 }}>_SystemUser</div>
        </div>
      </div>
    </header>
  );
}
