"use client";

import { useEffect, useMemo, useState, useCallback } from "react";
import Link from "next/link";
import {
  Handshake,
  Gauge,
  Bell,
  LineChart,
  List,
  Settings2,
  RotateCcw,
  X,
} from "lucide-react";

type StatusBarProps = {
  /** Hauteur de la topbar déjà présente au-dessus (par défaut 64) */
  headerHeight?: number;
  /** Titre affiché dans la status bar */
  title?: string;
};

export default function StatusBar({
  headerHeight = 64,
  title = "Textblock",
}: StatusBarProps) {
  // Palette
  const deepBlue = "#0f143b"; // proche de ta maquette
  const grey = "#d5d7dd";
  const greyDark = "#c8cad1";
  const orange = "#ff6600";

  // Hauteur de la status bar
  const statusHeight = 64;
  // Top du menu = topbar + statusbar
  const menuTop = headerHeight + statusHeight;

  const [open, setOpen] = useState(false);

  // Empêche le scroll quand le menu est ouvert
  useEffect(() => {
    const prev = document.body.style.overflow;
    document.body.style.overflow = open ? "hidden" : prev || "";
    return () => {
      document.body.style.overflow = prev;
    };
  }, [open]);

  // Fermer avec ESC
  useEffect(() => {
    const onKey = (e: KeyboardEvent) => {
      if (e.key === "Escape") setOpen(false);
    };
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, []);

  const toggle = useCallback(() => setOpen((o) => !o), []);

  const menuItems = useMemo(
    () => [
      { href: "/control", icon: Handshake, label: "CONTROL" },
      { href: "/dashboard", icon: Gauge, label: "DASHBOARD" },
      { href: "/events", icon: Bell, label: "EVENTS" },
      { href: "/data", icon: LineChart, label: "DATA" },
      { href: "/recipe", icon: List, label: "RECIPE" },
      { href: "/settings", icon: Settings2, label: "SETTINGS" },
    ],
    []
  );

  return (
    <div style={{ position: "relative", zIndex: 0 }}>
      {/* ---------- STATUS BAR ---------- */}
      <div
        style={{
          height: statusHeight,
          background: grey,
          color: "#0b1235",
          position: "relative",
          display: "flex",
          alignItems: "center",
          borderTop: `1px solid ${greyDark}`,
        }}
      >
        {/* triangle oblique à gauche */}
        <div
          aria-hidden
          style={{
            position: "absolute",
            left: 0,
            top: 0,
            width: 0,
            height: 0,
            borderTop: `${statusHeight}px solid ${grey}`,
            borderRight: `${statusHeight}px solid transparent`,
          }}
        />

        {/* Burger (3 barres orange) — uniquement ici */}
        <button
          onClick={toggle}
          title="Menu"
          aria-label="Open menu"
          style={{
            position: "absolute",
            left: 20,
            top: (statusHeight - 12 * 3 - 8 * 2) / 2,
            display: "flex",
            flexDirection: "column",
            gap: 10,
            cursor: "pointer",
            zIndex: 2,
            background: "transparent",
            border: "none",
            padding: 0,
          }}
        >
          {[0, 1, 2].map((i) => (
            <span
              key={i}
              style={{
                width: 100,
                height: 8,
                background: orange,
                borderRadius: 2,
                display: "block",
              }}
            />
          ))}
        </button>

        {/* "Status" + Titre */}
        <div
          style={{
            paddingLeft: statusHeight + 56,
            display: "flex",
            alignItems: "center",
            gap: 8,
          }}
        >
          <div style={{ padding: "0 24px", fontSize: 18, fontWeight: 700 }}>
            Status
          </div>
          <div style={{ padding: "0 24px", fontSize: 36, fontWeight: 800 }}>
            {title}
          </div>
        </div>

        {/* Champs valeurs (maquettes vides) */}
        {[
          ["F", "kN"],
          ["TT", "°C"],
          ["RS", "RPM"],
          ["ZP", "mm"],
        ].map(([label, unit], idx) => (
          <div
            key={label}
            style={{
              display: "flex",
              alignItems: "center",
              gap: 10,
              padding: "0 24px",
              borderLeft: `1px solid ${greyDark}`,
              marginLeft: idx === 0 ? 16 : 0,
            }}
          >
            <span style={{ fontSize: 14, fontWeight: 700 }}>{label}</span>
            <div
              aria-hidden
              style={{
                width: 100,
                height: 36,
                background: "#f5f6f8",
                border: `1px solid ${greyDark}`,
                borderRadius: 2,
              }}
            />
            <span style={{ fontSize: 14, fontWeight: 700 }}>{unit}</span>
          </div>
        ))}

        {/* Refresh à droite */}
        <div style={{ marginLeft: "auto", paddingRight: 24 }}>
          <button
            aria-label="Refresh"
            style={{
              width: 56,
              height: 56,
              borderRadius: "50%",
              background: deepBlue,
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              border: "none",
              cursor: "pointer",
            }}
          >
            <RotateCcw style={{ color: "white" }} />
          </button>
        </div>
      </div>

      {/* ---------- OVERLAY pour fermer ---------- */}
      {open && (
        <div
          onClick={() => setOpen(false)}
          aria-hidden
          style={{
            position: "fixed",
            top: menuTop,
            left: 0,
            right: 0,
            bottom: 0,
            background: "transparent",
            zIndex: 900,
          }}
        />
      )}

      {/* ---------- MENU LATERAL ---------- */}
      {open && (
        <aside
          role="dialog"
          aria-label="Navigation"
          style={{
            position: "fixed",
            top: menuTop,
            left: 0,
            bottom: 0,
            width: 220,
            background: deepBlue,
            zIndex: 1000,
            boxShadow: "0 6px 24px rgba(0,0,0,0.35)",
            display: "flex",
            flexDirection: "column",
            alignItems: "stretch",
            borderRight: "4px solid rgba(255,255,255,0.12)",
          }}
        >
          {/* Liste d’icônes (scrollable, scrollbar masquée) */}
          <nav className="menuScroll" style={{ paddingBottom: 12 }}>
            {menuItems.map((item, idx) => {
              const Icon = item.icon;
              return (
                <div key={item.href}>
                  {/* séparateur (pas au tout début) */}
                  {idx > 0 && (
                    <div
                      style={{
                        height: 1,
                        background: "rgba(255,255,255,0.25)",
                        margin: "14px 18px",
                      }}
                    />
                  )}

                  <Link
                    href={item.href}
                    aria-label={item.label}
                    onClick={() => setOpen(false)}
                    style={{
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                      height: 96,
                      color: "#fff",
                      textDecoration: "none",
                    }}
                  >
                    <div
                      style={{
                        width: 88,
                        height: 88,
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                      }}
                    >
                      <Icon size={48} color="#FFFFFF" />
                    </div>
                  </Link>
                </div>
              );
            })}
          </nav>

          {/* Bouton X en bas */}
          <div
            style={{
              marginTop: "auto",
              padding: "12px 12px 16px 12px",
              display: "flex",
              justifyContent: "flex-end",
            }}
          >
            <button
              onClick={() => setOpen(false)}
              aria-label="Close menu"
              style={{
                background: "transparent",
                border: "none",
                cursor: "pointer",
                padding: 8,
              }}
            >
              <X color="#fff" />
            </button>
          </div>
        </aside>
      )}

      {/* Styles globaux pour masquer la scrollbar du menu */}
      <style jsx global>{`
        .menuScroll {
          overflow-y: auto;
        }
        .menuScroll::-webkit-scrollbar {
          width: 0;
          height: 0;
        }
        .menuScroll {
          scrollbar-width: none; /* Firefox */
          -ms-overflow-style: none; /* IE/Edge legacy */
        }
      `}</style>
    </div>
  );
}
